import React from 'react'
import { Link } from 'gatsby';

import Container from 'components/Container'
import Button from 'components/Button'
import Box from 'components/Box';
import Text from 'components/Text';

const pages = {
  dram: [
    { name: 'DRAM', href: '/dram/dram' },
    { name: 'DRAM Technology', href: '/dram/tech' }
  ],
  flash: [
    { name: 'Flash', href: '/flash/flash' },
    { name: '3年MCP', href: '/flash/mcp' },
    { name: 'Flash Technology', href: '/flash/tech' },
  ],
}

const HomePage = ({ name }) => {

  return (
    <Container maxWidth="640px">
      <Text>Roadmap Tools:</Text>
      <Box mx="-0.5em">
        {pages[name].map((page, p) => (
          <Button key={p} m="0.5em" as={Link} to={page.href}>{page.name} {'>'}</Button>
        ))}
      </Box>
    </Container>
  )
}

export default HomePage
