import React from 'react'
import useGapi from 'contexts/gapi/useGapi'
import Container from 'components/Container'
import Text from 'components/Text'

const withAuthChecker = (Page) => props => {
  const {
    isSignedIn,
    AuthButton,
  } = useGapi()
  return isSignedIn ? <Page {...props} /> : (
    <Container>
      <Text>請先登入</Text>
      <AuthButton />
    </Container>
  )
}

export default withAuthChecker
