import React, { useContext, useCallback } from 'react'

import Button from '../../components/Button'

import context from './context'

export default () => {
  const { isSignedIn, signIn, signOut, ...rest } = useContext(context)
  const AuthButton = useCallback(
    () => (
      <Button.Outline onClick={isSignedIn ? signOut : signIn}>
        {isSignedIn ? 'Logout' : 'Login'}
      </Button.Outline>
    ),
    [isSignedIn]
  )
  return {
    AuthButton,
    isSignedIn,
    signIn,
    signOut,
    ...rest,
  }
}
